.nav {
  z-index: 100;
  width: 100%;
  position: fixed;
  /* height: 100%; */
}



.navSmallNav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
}

.container-bar {
    right: 10px;
    display: inline-block;
    cursor: pointer;
    padding: 10px;
    margin-right:5%;
    border: 1px solid #004744;
    z-index: 100;
    transition:0.3s;
  }

  .container-bar.change  {
    border: 1px solid rgba(255, 255, 255, 0.3);
  }
  
  .bar1, .bar2, .bar3 {
    width: 35px;
    height: 3px;
    background-color: #004744;
    margin: 7px 0;
    transition: 0.4s;
    
  }
  .container-bar:hover {
    border: 1px solid rgba(0, 71, 68,.7);
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
  }

  
  .change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
    background-color: #f0fffe;
  }
  
  .change .bar2 {opacity: 0;
    background-color: #f0fffe;
  }
  
  .change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
    background-color: #f0fffe;
  }

  .aboutStyle .navSmallNav h1{
    color: #f0fffe;
  }

  .aboutStyle .navSmallNav .bar1{
    background-color: #f0fffe;
  }

  .aboutStyle .navSmallNav .bar2{
    background-color: #f0fffe;
  }

  .aboutStyle .navSmallNav .bar3{
    background-color: #f0fffe;
  }

  .aboutStyle .container-bar {
    border: 1px solid  rgba(255, 255, 255, 0.3);
  }


  .navSmallNav h1{
      font-size: 40px;
